body,
html,
#root {
  height: 100%;
  margin: 0;
}

/* body {
  background-image: url(../../blue-waves-background.jpg);
  backdrop-filter: saturate(20%) hue-rotate(-80deg);
} */

html {
  font-size: 18px;
}

h1 {
  font-weight: bold;
}

h2 {
  font-weight: normal;
}

p {
  font-weight: lighter;
}

.Title {
  padding: .77rem 0;
  text-align: center;
  font-size: 4rem;
  color: #000;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  height: 100%;
  padding: 0 7% 10% 7%;
  
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-family: 'Calibri', sans-serif;
  color: #000;
}

.Tracklists {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-auto-flow: column;
  gap: 7%;
}

.EachTracklist {
  width: 100%;
}

@media only screen and (max-width: 850px) {
  .Tracklists {
    display: block;
    width: 100%;
  }

  .EachTracklist {
    margin-bottom: 3rem;
  }
}